@font-face {
	font-family: 'Inter';
	src: url('../fonts/Inter_Regular/Inter_Regular.eot');
	src: local('Inter_Regular'), local('../fonts/Inter_Regular/Inter_Regular'),
		url('../fonts/Inter_Regular/Inter_Regular.eot?#iefix') format('embedded-opentype'),
		url('../fonts/Inter_Regular/Inter_Regular.woff') format('woff'),
		url('../fonts/Inter_Regular/Inter_Regular.ttf') format('truetype');
	font-weight: 400;
	font-style: normal;
	font-display: swap;
}
